@import url(https://fonts.googleapis.com/css2?family=Rampart+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Festive&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);

* {
  padding: 0;
  margin: 0;
}
h1 {
  font-family: "Rampart One", cursive;
  font-size: 45px;
  padding: 20px;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: radial-gradient(#ffcc66, #ff9d1c);
}
.container {
  border: 2px dashed;
  border-radius: 20px;
  height: 280px;
  width: 500px;
  justify-content: center;
  padding: 10px;
}
button,
input[type="file"]::file-selector-button {
  color: rgb(0, 0, 0) !important;
  text-transform: uppercase;
  text-decoration: none;
  background: radial-gradient(#f7c96f, #fd8f00); /* #ffdc96 */
  padding: 15px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
  font-family: "Akaya Kanadaka", cursive;
}
button:hover,
input[type="file"]::file-selector-button:hover {
  background: #434343;
  letter-spacing: 1px;
  box-shadow: 5px 40px -10px rgba(0, 0, 0, 0.57);
  transition: all 0.4s ease 0s;
  color: #fff !important;
}
.manual {
  display: flex;
  padding: 50px;
  justify-content: center;
}

.minion {
  position: absolute;
  top: calc(48% - (250px / 2));
  left: calc(50% - (140px / 2));
  width: 140px;
}
.minion * {
  position: absolute;
}
.minion .goggle-strap {
  top: 68px;
  height: 3px;
  background: black;
  width: 16px;
  z-index: 6;
}
.minion .goggle-strap.left {
  left: -2px;
}
.minion .goggle-strap.left:before,
.minion .goggle-strap.left:after {
  border-right: 16px solid #444444;
}
.minion .goggle-strap.right {
  right: -2px;
}
.minion .goggle-strap.right:before,
.minion .goggle-strap.right:after {
  border-left: 16px solid #444444;
}
.minion .goggle-strap:before,
.minion .goggle-strap:after {
  height: 6px;
}
.minion .goggle-strap:before {
  top: -7px;
  border-top: 2px solid transparent;
}
.minion .goggle-strap:after {
  top: 3px;
  border-bottom: 2px solid transparent;
}
.minion .goggle-strap-link {
  top: 58px;
  height: 25px;
  width: 5px;
  background: #999999;
  z-index: 7;
}
.minion .goggle-strap-link.left {
  left: 12px;
}
.minion .goggle-strap-link.right {
  right: 12px;
}
.minion .goggle-frame {
  top: 40px;
  height: 50px;
  width: 50px;
  border: 5px solid #cccccc;
  border-radius: 50%;
  overflow: hidden;
  z-index: 8;
}
.minion .goggle-frame.left {
  left: 30%;
  margin-left: -30px;
}
.minion .goggle-frame.left .goggle {
  left: -1px;
}
.minion .goggle-frame.right {
  right: 30%;
  margin-right: -30px;
}
.minion .goggle-frame.right .goggle {
  right: -1px;
}
.minion .goggle-frame .goggle {
  height: 50px;
  width: 50px;
  border: 3px solid #666666;
  border-radius: 50%;
}
.minion .goggle-frame .goggle {
  display: flex;
}
.minion .goggle-frame .goggle .eye {
  position: absolute;
  width: 50px;
  height: 50px;
  display: block;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 5px 45px rgba(0, 0, 0, 0.2), inset 0 0 15px rgb(189, 189, 189),
    inset 0 0 15px rgb(134, 134, 134);
}
.minion .goggle-frame .goggle .eye::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 35px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #000;
  border: 10px solid #94663c;
  box-sizing: border-box;
}
.minion .goggle-shadow {
  height: 75px;
  top: 45px;
  border-radius: 50%;
  background-image: radial-gradient(
    rgba(0, 0, 0, 0.8) 0%,
    rgba(0, 0, 0, 0) 50%
  );
  z-index: 5;
}
.minion .mouth {
  top: 110px;
  left: 50%;
  margin-left: -30px;
  height: 20px;
  width: 60px;
  border-bottom: 2px solid #de9a33;
  border-right: 1px solid tranparent;
  border-left: 1px solid tranparent;
  border-radius: 70%;
  z-index: 5;
  box-shadow: inset 0 -1px 2px -1px rgba(0, 0, 0, 0.3);
}

.dropbox {
  color: rgb(0, 0, 0);
  font: 1em sans-serif;
  background-size: cover;
  height: 240px;
  width: 500px;
  border-radius: 20px;
}
.dropbox {
  justify-content: center;
  align-items: center;
  text-align: center;
}
p {
  text-align: center;
  font-size: 25px;
  justify-content: center;
  font-family: "Dancing Script", cursive;
  padding: 20px;
}
.filename {
  height: 20px;
  width: 500px;
  text-decoration: none;
  text-align: center;
}
li {
  list-style: none;
}

